/**
 * RISIVO CRM - Demo Email Verification API
 * 
 * Verifies the email and allows password setup
 */

import { NextRequest, NextResponse } from 'next/server'
import { cookies } from 'next/headers'
import { prisma } from '@/lib/db'
import { sendAdminNotification, getAdminVerifiedEmail } from '@/lib/email/sendgrid'
import bcrypt from 'bcryptjs'
import jwt from 'jsonwebtoken'

export const runtime = 'nodejs'
export const dynamic = 'force-dynamic'

// GET - Verify token and return demo info
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const token = searchParams.get('token')

    if (!token) {
      return NextResponse.json(
        { valid: false, error: 'Verification token is required' },
        { status: 400 }
      )
    }

    if (!prisma) {
      return NextResponse.json(
        { valid: false, error: 'Database not available' },
        { status: 503 }
      )
    }

    const demoAccess = await prisma.demoAccess.findUnique({
      where: { verificationToken: token },
    })

    if (!demoAccess) {
      return NextResponse.json(
        { valid: false, error: 'Invalid or expired verification link' },
        { status: 404 }
      )
    }

    // Check if verification token is expired (24 hours)
    if (demoAccess.verificationSentAt) {
      const tokenAge = Date.now() - new Date(demoAccess.verificationSentAt).getTime()
      const maxAge = 24 * 60 * 60 * 1000 // 24 hours
      
      if (tokenAge > maxAge) {
        return NextResponse.json(
          { valid: false, error: 'Verification link has expired. Please sign up again.' },
          { status: 410 }
        )
      }
    }

    // Check if already verified and has password
    if (demoAccess.emailVerified && demoAccess.password) {
      return NextResponse.json({
        valid: true,
        alreadyVerified: true,
        name: demoAccess.name,
        email: demoAccess.email,
        message: 'Your email is already verified. Please log in.',
      })
    }

    return NextResponse.json({
      valid: true,
      alreadyVerified: false,
      name: demoAccess.name,
      email: demoAccess.email,
      company: demoAccess.company,
    })

  } catch (error) {
    console.error('[Demo Verify GET] Error:', error)
    return NextResponse.json(
      { valid: false, error: 'Failed to verify token' },
      { status: 500 }
    )
  }
}

// POST - Set password and activate demo access
export async function POST(request: Request) {
  console.log('[Demo Verify POST] Request received')
  
  try {
    const body = await request.json()
    const { token, password } = body

    if (!token || !password) {
      return NextResponse.json(
        { success: false, error: 'Token and password are required' },
        { status: 400 }
      )
    }

    // Validate password strength
    if (password.length < 8) {
      return NextResponse.json(
        { success: false, error: 'Password must be at least 8 characters long' },
        { status: 400 }
      )
    }

    if (!prisma) {
      return NextResponse.json(
        { success: false, error: 'Database not available' },
        { status: 503 }
      )
    }

    const demoAccess = await prisma.demoAccess.findUnique({
      where: { verificationToken: token },
    })

    if (!demoAccess) {
      return NextResponse.json(
        { success: false, error: 'Invalid or expired verification link' },
        { status: 404 }
      )
    }

    // Check if verification token is expired (24 hours)
    if (demoAccess.verificationSentAt) {
      const tokenAge = Date.now() - new Date(demoAccess.verificationSentAt).getTime()
      const maxAge = 24 * 60 * 60 * 1000 // 24 hours
      
      if (tokenAge > maxAge) {
        return NextResponse.json(
          { success: false, error: 'Verification link has expired. Please sign up again.' },
          { status: 410 }
        )
      }
    }

    // Hash the password
    const hashedPassword = await bcrypt.hash(password, 12)

    // Update the demo access
    const updatedDemo = await prisma.demoAccess.update({
      where: { id: demoAccess.id },
      data: {
        password: hashedPassword,
        emailVerified: true,
        verificationToken: null, // Clear the token
        status: 'active',
        firstAccessAt: new Date(),
      },
    })

    console.log('[Demo Verify POST] Demo access activated:', updatedDemo.id)

    // Send admin notification
    const adminEmail = getAdminVerifiedEmail({
      name: updatedDemo.name,
      email: updatedDemo.email,
      company: updatedDemo.company || undefined,
      verifiedTime: new Date(),
    })
    
    try {
      const adminEmailSent = await sendAdminNotification(adminEmail)
      if (adminEmailSent) {
        console.log('[Demo Verify] Admin notification sent successfully')
      } else {
        console.error('[Demo Verify] Admin notification failed to send')
      }
    } catch (err) {
      console.error('[Demo Verify] Failed to send admin notification:', err)
    }

    // Create JWT and log them in
    const jwtSecret = process.env.NEXTAUTH_SECRET || process.env.AUTH_SECRET
    if (!jwtSecret) {
      return NextResponse.json(
        { success: false, error: 'Server configuration error' },
        { status: 500 }
      )
    }

    const jwtPayload = {
      id: `demo-${updatedDemo.id}`,
      email: updatedDemo.email,
      name: updatedDemo.name,
      role: 'demo',
      isMasterAdmin: false,
      status: 'active',
      agencyId: null,
      agencyName: 'Demo',
      sub: `demo-${updatedDemo.id}`,
    }

    const jwtToken = jwt.sign(jwtPayload, jwtSecret, { expiresIn: '7d' })

    // Set cookies
    const cookieStore = await cookies()
    const isProduction = process.env.NODE_ENV === 'production'
    const cookieName = isProduction ? '__Secure-authjs.session-token' : 'authjs.session-token'

    cookieStore.set(cookieName, jwtToken, {
      httpOnly: true,
      secure: isProduction,
      sameSite: 'lax',
      path: '/',
      maxAge: 7 * 24 * 60 * 60,
    })

    cookieStore.set('demo_access_token', updatedDemo.accessToken, {
      httpOnly: true,
      secure: isProduction,
      sameSite: 'lax',
      path: '/',
      maxAge: 7 * 24 * 60 * 60,
    })

    return NextResponse.json({
      success: true,
      message: 'Your account has been verified and activated!',
      user: {
        id: jwtPayload.id,
        email: updatedDemo.email,
        name: updatedDemo.name,
        company: updatedDemo.company,
      },
    })

  } catch (error) {
    console.error('[Demo Verify POST] Error:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to verify account' },
      { status: 500 }
    )
  }
}
